<template>
	<section class="blog-manage">
		<div class="section-title">
		    <h2 class="section-title__text" @click='toggle'>Керування {{this.title}}</h2>
		</div>
		<!-- /.section-title -->
		<div class="post-list" v-if='show'>
			<div class="admin-content__add text-right">
				<router-link :to='newItem' tag='button' class='btn btn-primary mb-2'>Нова робота</router-link>
			</div>
			<!-- /.admin-content__add -->
			<div class="empty-list" v-if='data.length === 0'>
				Немає даних!
			</div>
			<div v-else class="table-content table-responsive">
			    <table>
			        <thead>
			            <tr class="title-top">
			                <th>Назва</th>
			                <th>Дата публікації</th>
			                <th>Дії</th>
			            </tr>
			        </thead>
			        <tbody>
			            <tr v-for='item in data' :key='item.id'>
			                <td>
			                	<img :src="item.img" class="img-responsive" alt="product img" />
			                </td>
			                <td>
			                	{{item.title}}
			                </td>
			                
			                <td class="actions">
			           			<router-link
			           				tag='button' class='d-block m-auto btn btn-primary' 
									:to="{ name: edit, params: { title: $_friendlyUrl(item.title), id: item.id }}"
								>
									Редагувати
								</router-link>
			                	<button class='d-block m-auto btn btn-primary'  @click='deleteItem(item.id)'>Видалити</button>
			                </td>
			            </tr>
			        </tbody>
			    </table>
			</div>
			
		</div>
	</section>
	
</template>
<script>
	import { db } from '@/main'

	export default {
		name: 'manageContent',
		props: ['title', 'collection', 'edit', 'newItem'],
		data() {
			return {
				data: [],
				show: false,
			}
		},
		methods: {
			deleteItem(id) {
				db.collection(this.collection).doc(id).delete()
			},
			toggle() {
				this.show = !this.show;
			}
		},
		firestore () {
		   return {
		     data: db.collection(this.collection)
		   }
		},
	}
</script>